<template>
  <div class="recharge-channel">
    <nav-head title="充值" FixedOrTransparency="isFixed NoTransparency"/>
    <div class="info-wrap">
      <router-link to="moneyLog" tag="div" class="row-item">
        <div class="value">{{UserInfo.diamond}}</div>
        <div class="describe"><span class="icon-balance-365 icon"></span><span class="txt">我的余额</span></div>
      </router-link>
      <router-link to="/redEnvelopesList" tag="div" class="row-item">
        <div class="value">{{UserInfo.coupon}}</div>
        <div class="describe"><span class="icon-redPack icon"></span><span class="txt">红包</span></div>
      </router-link>
    </div>
    <div class="item-wrap">
      <label class="item"
             v-for="item in MoneyList"
             :item="item"
             :key="item.id">
        {{item.name}}<input name="price" v-bind:value="item.price" type="radio" @click="selectPrice"/>
      </label>
    </div>
    <div class="padding-0-15" style="color: #CCC;"><input class="price" type="text" @input="handleInputNum" maxlength="7" v-model="price" placeholder="请输入充值金额">&emsp;注：充值金额限定为100至100000之间</div>
    <div class="padding-0-15" ><mt-button size="large" v-bind:disabled="!price" @click.native="payment" class="btn-recharge" v-bind:class="{'available': price }">确认充值</mt-button></div>
  </div>
</template>
<script>//
import NavHead from '@/components/NavHead.vue'
import { Button, Toast } from 'mint-ui'
import { mapState } from 'vuex'
import http from '../../../tool/http'

export default {
  name: 'recharge365',
  data () {
    return {
      price: '',
      id: this.$route.query.id,
      MoneyList: ''
    }
  },
  methods: {
    selectPrice (e) {
      this.price = e.target.value
      var ele = document.getElementsByClassName('item')
      for (var i = 0; i < ele.length; i++) {
        ele[i].className = 'item'
      }
      e.target.parentNode.className = 'item on'
    },
    payment () {
      if (this.price) {
        http.postOverseasRecharge({ recharge_channel_id: this.id, money: this.price }).then(data => {
          if (data) {
            const winOpen = window.open(data.target_url, '_blank')
            setTimeout(function () {
              if (winOpen == null || typeof (winOpen) === 'undefined') {
                // ios打不开
                window.location.href = data.target_url
              }
            }, 500)
          }
        }).catch(mag => {
          Toast('错误')
        })
      } else {
        Toast('请输入充值金额')
      }
    },
    handleInputNum (e) {
      this.price = e.target.value.replace(/[^\d]/g, '')
    }
  },
  components: {
    NavHead,
    [Button.name]: Button
  },
  computed: {
    ...mapState({
      UserInfo: state => state.mine.User
    })
  },
  created () {
    http.getMoneyList().then(data => {
      if (data) {
        this.MoneyList = data
      }
    })
  }
}
</script>
<style scoped lang="scss">
  .info-wrap {
    background-color: $background-light-color;
    text-align: center;
    padding: 17px 0 17px 0;
    div.row-item {
      display: inline-block;
      width: 50%;
      font-size: 16px;
      color: #333;
      border-right: 1px solid $border-color;
      > div.value {
        font-weight: bold;
        color: $font-color;
        font-size: 20px;
      }
      > div.describe {
        position: relative;
        height: 21px;
        margin-top: 6px;
        > span.icon {
          position: absolute;
          margin-top: -3px;
          margin-left: 0;
        }
        > span.txt{
          display: inline-block;
          margin-left: 30px;
          font-size: 12px;
          color: #ccc;
        }
      }
    }
    div.row-item:last-child {
      border: none;
    }
  }

  .item-wrap {
    margin: 20px 0 10px 0;
    padding: 0 15px;
    text-align: left;
    label.item {
      display: inline-block;
      width: 30.8%;
      height: 55px;
      line-height: 55px;
      text-align: center;
      font-size: 20px;
      font-weight: bold;
      background-color: $background-light-color;
      border: 1px solid $background-light-color;
      border-radius: 5px;
      color: $font-color;
      position: relative;
      margin: 0 3.8% 15px 0;
      > input {
        position: absolute;
        left: 0;
        top: 0;
        width: 100%;
        height: 100%;
        opacity: 0;
      }
    }
    label.item:nth-child(3n) {
      margin-right: 0;
    }
    label.item.on {
      border: 1px solid $back-color;
    }
  }

  input.price {
    border: none;
    outline: none;
    display: block;
    width: 100%;
    padding: 0 15px;
    font-size: 15px;
    height: 46px;
    line-height: 46px;
    margin-bottom: 10px;
    color: #999;
    background: $background-light-color;
    border-radius: 5px;
  }

  button.btn-recharge {
    border: none;
    background-color: #ccc;
    color: white;
    box-shadow: none;
    height: 48px;
    font-size: 17px;
    margin-top: 15px;
  }
  button.btn-recharge.available{
    background-color: $back-color;
    @if $IF365 { color: #131313; }
  }
  .margin-left-30{
    margin-left: 50px;
  }
  .padding-0-15 {
    padding: 0 15px;
  }
</style>
